// frontend/src/app/layout.tsx
import type { Metadata } from 'next';
import { Inter, Manrope } from 'next/font/google';
import './globals.css';

const inter = Inter({
  subsets: ['latin'],
  variable: '--font-inter',
  display: 'swap',
  preload: true,
});

const manrope = Manrope({
  subsets: ['latin'],
  variable: '--font-manrope',
  display: 'swap',
  preload: true,
});

export const metadata: Metadata = {
  title: 'VIBECRO - Solutions Digitales Innovantes',
  description: 'IoT, IA & Développement sur-mesure pour transformer votre entreprise',
  keywords: ['IoT', 'IA', 'développement web', 'solutions digitales', 'VIBECRO'],
  authors: [{ name: 'VIBECRO' }],
  robots: {
    index: true,
    follow: true,
    googleBot: {
      index: true,
      follow: true,
      'max-video-preview': -1,
      'max-image-preview': 'large',
      'max-snippet': -1,
    },
  },
  openGraph: {
    type: 'website',
    locale: 'fr_FR',
    url: 'https://vibecro.com',
    title: 'VIBECRO - Solutions Digitales Innovantes',
    description: 'IoT, IA & Développement sur-mesure pour transformer votre entreprise',
    siteName: 'VIBECRO',
    images: [
      {
        url: '/images/og-image.jpg',
        width: 1200,
        height: 630,
        alt: 'VIBECRO Solutions Digitales',
      },
    ],
  },
  twitter: {
    card: 'summary_large_image',
    title: 'VIBECRO - Solutions Digitales Innovantes',
    description: 'IoT, IA & Développement sur-mesure pour transformer votre entreprise',
    images: ['/images/twitter-image.jpg'],
    creator: '@vibecro',
  },
  viewport: {
    width: 'device-width',
    initialScale: 1,
    maximumScale: 1,
  },
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="fr" className={`${inter.variable} ${manrope.variable}`}>
      <head>
        <link rel="preconnect" href="https://fonts.googleapis.com" />
        <link rel="preconnect" href="https://fonts.gstatic.com" crossOrigin="anonymous" />
        
        {/* Style pour les dégradés avec les 3 couleurs */}
        <style
          dangerouslySetInnerHTML={{
            __html: `
              /* Dégradés avec la couleur primaire #e38f00 */
              .text-gradient-primary {
                background: linear-gradient(135deg, #e38f00 0%, #f7b333 100%);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
              }
              
              .bg-gradient-primary {
                background: linear-gradient(135deg, #e38f00 0%, #f7b333 100%);
              }
              
              .bg-gradient-to-r-primary {
                background: linear-gradient(to right, #e38f00, #d48500, #c67b00);
              }
              
              /* Animation au scroll */
              @keyframes fadeInUp {
                from {
                  opacity: 0;
                  transform: translateY(20px);
                }
                to {
                  opacity: 1;
                  transform: translateY(0);
                }
              }
              
              .animate-fadeInUp {
                animation: fadeInUp 0.6s ease-out forwards;
              }
              
              /* Animation de spin lente */
              @keyframes spin-slow {
                from {
                  transform: rotate(0deg);
                }
                to {
                  transform: rotate(360deg);
                }
              }
              
              .animate-spin-slow {
                animation: spin-slow 3s linear infinite;
              }
              
              /* Pattern de grille pour l'arrière-plan */
              .bg-grid-pattern {
                background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23e38f00' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
              }
              
              /* Amélioration de l'accessibilité */
              .sr-only {
                position: absolute;
                width: 1px;
                height: 1px;
                padding: 0;
                margin: -1px;
                overflow: hidden;
                clip: rect(0, 0, 0, 0);
                white-space: nowrap;
                border: 0;
              }
              
              .sr-only:focus {
                position: fixed;
                top: 4px;
                left: 4px;
                width: auto;
                height: auto;
                padding: 0.75rem 1rem;
                margin: 0;
                overflow: visible;
                clip: auto;
                white-space: normal;
                background-color: #e38f00;
                color: white;
                z-index: 9999;
                border-radius: 0.5rem;
              }
              
              /* Smooth scrolling */
              html {
                scroll-behavior: smooth;
              }
            `,
          }}
        />
      </head>
      <body className="font-sans antialiased bg-white text-black">
        {/* Skip to content pour l'accessibilité */}
        <a
          href="#main-content"
          className="sr-only focus:not-sr-only focus:fixed focus:top-4 focus:left-4 bg-[#e38f00] text-white px-4 py-2 rounded-lg z-[9999]"
        >
          Aller au contenu principal
        </a>
        
        {/* Header sera injecté par chaque page */}
        <main id="main-content" className="min-h-screen bg-white">
          {children}
        </main>
        
        {/* Footer sera injecté par chaque page */}
      </body>
    </html>
  );
}